import React, { useState } from 'react';
import './index.css';
import Profile from './components/Profile';
import LanguageSelector from './components/LanguageSelector';
import { useTranslation } from 'react-i18next';
import { Box, Tabs, Tab } from '@mui/material';
import { styled } from '@mui/system';

const StyledTabs = styled(Tabs)({
  backgroundColor: '#fff',
  borderRadius: '24px',
  boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.1)',
  '& .MuiTabs-indicator': {
    display: 'none',
  },
});

const StyledTab = styled((props) => <Tab disableRipple {...props} />)(
  ({ theme }) => ({
    textTransform: 'none',
    minWidth: 0,
    fontWeight: theme?.typography?.fontWeightRegular ?? 400,
    marginRight: theme?.spacing(1) ?? '8px',
    color: 'rgba(0, 0, 0, 0.6)',
    '&.Mui-selected': {
      color: theme?.palette?.primary?.main ?? '#1976d2',
      fontWeight: theme?.typography?.fontWeightMedium ?? 500,
    },
    '&:hover': {
      color: theme?.palette?.primary?.main ?? '#1976d2',
      opacity: 1,
    },
  }),
);

function App() {
  const { t } = useTranslation(['menu', 'content']);
  const [activeTab, setActiveTab] = useState('about');

  const handleChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const renderContent = () => {
    switch (activeTab) {
      case 'about':
        return <Profile />;
      case 'career':
        return <div>{t('career', { ns: 'content' })}</div>;
      case 'skills':
        return <div>{t('skills', { ns: 'content' })}</div>;
      case 'hobbies':
        return <div>{t('hobbies', { ns: 'content' })}</div>;
      default:
        return <Profile />;
    }
  };

  return (
    <div className="App">
      <Box sx={{ width: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center', mt: 2 }}>
        <StyledTabs
          value={activeTab}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs"
        >
          <StyledTab label={t('about', { ns: 'menu' })} value="about" />
          <StyledTab label={t('career', { ns: 'menu' })} value="career" />
          <StyledTab label={t('skills', { ns: 'menu' })} value="skills" />
          <StyledTab label={t('hobbies', { ns: 'menu' })} value="hobbies" />
        </StyledTabs>
        <Box sx={{ ml: 2, display: 'flex', alignItems: 'center' }}>
          <LanguageSelector />
        </Box>
      </Box>
      <div className="p-4">
        {renderContent()}
      </div>
    </div>
  );
}

export default App;
